























import {Component, Watch, Vue} from 'vue-property-decorator';
import $ from 'jquery'
import bus from "../../common/bus";
import {mixinNavLinks} from "@/js/mixins/navLinks";
import logoImg from '@/assets/img/logo.png'
import NavLinks from "@/js/components/layouts/header/NavLinks.vue";

@Component({
    components: {NavLinks},
    mixins: [mixinNavLinks],
    data: () => ({
        logoImg
    })
})
export default class NavBar extends Vue {
    visible = false;

    mounted(): void {
        bus.$on('nav-bar-hide', () => {
            this.hide();
        });

        bus.$on('nav-bar-show', () => {
            this.show()
        });
    }

    show(): void {
        this.visible = true
    }

    hide(): void {
        this.visible = false
    }

    handleClick(event: Event): void {
        const $body = $(this.$refs['nav-bar']);

        if ($body.get(0) === event.target) {
            this.hide()
        }
    }

    handleClose(): void {
        this.hide()
    }

    @Watch('visible')
    watchShow(val: boolean): void {
        if (val) {
            $('body').css('overflow', 'hidden')
        } else {
            $('body').css('overflow', '')
        }
    }

    @Watch('$route')
    watchRoute(): void {
        this.hide()
    }
}
